// *** Story component
import React, { Component } from 'react';
import "./style.css";
import { Container, Row, Col, Form, Button, ListGroup } from "react-bootstrap";
import fire from './firebase';
import moment from 'moment';

class Guestbook extends Component {
  constructor(props) {
    super(props);
    // <- set up react state
    this.state = {
      signatures: [],
      thoughts: [],
      time: []
    };
  }

  componentWillMount(){
    /* Create reference in Firebase Database */
    let signaturesRef = fire.database().ref('signatures').orderByKey().limitToLast(100);
    let thoughtsRef = fire.database().ref('thoughts').orderByKey().limitToLast(100);
    let timeRef = fire.database().ref('time').orderByKey().limitToLast(100);
    signaturesRef.on('child_added', snapshot => {
      /* Update React state when added at Firebase Database */
      let signature = { text: snapshot.val(), id: snapshot.key };
      this.setState({ signatures: [signature].concat(this.state.signatures) });
    })
    thoughtsRef.on('child_added', snapshot => {
      /* Update React state when added at Firebase Database */
      let thought = { text: snapshot.val(), id: snapshot.key };
      this.setState({ thoughts: [thought].concat(this.state.thoughts) });
    })
    timeRef.on('child_added', snapshot => {
      /* Update React state when added at Firebase Database */
      let time = { text: snapshot.val(), id: snapshot.key };
      this.setState({ time: [time].concat(this.state.time) });
    })
  }
  
  addMessage(e){
    e.preventDefault(); // <- prevent form submit from reloading the page
    /* Send to Firebase */
    fire.database().ref('signatures').push( this.inputSignature.value );
    fire.database().ref('thoughts').push( this.inputThought.value );
    fire.database().ref('time').push( moment().format("MM/DD/YYYY") );
    this.inputSignature.value = ''; // <- clear the input
    this.inputThought.value = ''; // <- clear the input
  }

  render() {
    return (
      <Container id="guestbook">
        <Row>
          <Col id="titleguestbook">
            Guestbook
          </Col>
        </Row>
        <Row>
          <Col id="titlesign">
            Please sign our guestbook...
          </Col>
        </Row>
        <Row>
          <Col>
            <Form onSubmit={this.addMessage.bind(this)}>
              <Row className="forminputs">
                <Col sm={{ span: 4, offset: 4 }}>
                  <p>
                    <Form.Control maxLength={30} className="forminput border border-secondary" type="string" ref={ signature => this.inputSignature = signature } name="Name" placeholder="Name" required />
                  </p>
                </Col>
              </Row>
              <Row> 
                <Col sm={{ span: 6, offset: 3 }}>
                  <p>
                    <Form.Group controlId="exampleForm.ControlTextarea1">
                      <Form.Control as="textarea" rows="1" maxLength={60} className="forminput border border-secondary" type="string" ref={ thought => this.inputThought = thought } name="Thought" placeholder="Leave a thought..." />
                    </Form.Group>
                  </p>
                </Col>
              </Row>
              <Row>
                <Col>
                  <Button id="signbutton" variant="primary" type="submit">
                    Submit
                  </Button>
                </Col>
              </Row>
              <Row>
                <Col id="thoughts">
                  {/* Render the list */}
                  <ListGroup>
                    <ListGroup.Item>
                      <Row>
                        <Col sm={1}>
                          {
                            this.state.time.map(
                              time => <li key={time.id}>{time.text}</li>
                            )
                          }
                        </Col>
                        <Col sm={3}>
                          {
                            this.state.signatures.map(
                              signature => <li key={signature.id}>{signature.text}</li>
                            )
                          }
                        </Col>
                        <Col sm={6}>
                          {
                            this.state.thoughts.map(
                              thought => <li key={thought.id}>{thought.text}</li>
                            )
                          }    
                        </Col>
                      </Row>
                    </ListGroup.Item>
                  </ListGroup>
                </Col>
              </Row>
            </Form>
          </Col>
        </Row>
      </Container>
    );
  }
}

// Export function for importing into /pages
export default Guestbook;
